.container{
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 200px;
    background: #ffffff;
    padding-bottom: 30px;
    width: 600px;

}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
}

.text {
    color: #1009e0;
    font-size: 48px;
    font-weight: 700;
}

.underline {
    width: 61px;
    height: 6px;
    background: #1009e0;
    border-radius: 9px;
}

.inputs{
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.input {
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 80px;
    background: #eaeaea;
    border-radius: 6px;
}


.input input {
    height: 50px;
    width: 400px;
    background: transparent;
    border: none;
    outline: none;
    color: #797979;
    font-size: 19px;
}

.img {
    margin: 15px;
}

.submit-container {
    display:flex;
    gap:30px;
    margin:60px auto;
}

.submit {
    display:flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: #ffffff;
    background: #1009e0;
    border-radius: 50px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;
}